<template>
	<div class="">
		<h-pull class="" :topMethod="refresh" :bottomMethod="loadmore" ref="pull">
			<ul slot="content">
				<li v-for="item in lists">{{item}}</li>
			</ul>
		</h-pull>
	</div>
</template>

<script>
	export default{
		data(){
			return {
				lists:[123,123,123,123,123,123,123,242,545,4545,45454,123,123,123,123,123,123,123,242,545,4545,45454,123,123,123,123,123,123,123,242,545,4545,45454,123,123,123,123,123,123,123,242,545,4545,45454,'end']
			}
		},
		methods:{
			refresh(){ 
				console.log('refresh');
				var x=8888;
				for(var i=0;i<x;i++){
					console.log(i);
				}
				this.$refs.pull.onTopLoaded();
			},
			loadmore(){
				console.log('loadmore');
			}
		}
	}
</script>

